.sidebar {
  position: fixed;
  left: 0;
  top: 4.9rem;
  bottom: 0;
  width: 15rem;
  background-color: var(--nav-bg);
  border-right: thin solid var(--text-color);
  color: var(--text-color);
  padding: 1rem;
  transition: transform 0.3s ease;
  overflow-y: auto;
  z-index: 900;
}

.sidebar.open {
  transform: translateX(0);
}

.topic {
  margin-bottom: 1.5rem;
}

.topic-header {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.subtopics {
    list-style: none;
    padding-left: 1rem;
}

.subtopics li {
  padding: 0.25rem 0rem;
}
.subtopics li a,
.subtopics li .no-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
  color: inherit;
}

@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
  }
}
